	module.exports = {"pathContext":{"docHtml":"<h1>expand-brackets <a href=\"https://www.npmjs.com/package/expand-brackets\"><img src=\"https://img.shields.io/npm/v/expand-brackets.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/expand-brackets\"><img src=\"https://img.shields.io/npm/dm/expand-brackets.svg?style=flat\" alt=\"NPM downloads\"></a> <a href=\"https://travis-ci.org/jonschlinkert/expand-brackets\"><img src=\"https://img.shields.io/travis/jonschlinkert/expand-brackets.svg?style=flat\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Expand POSIX bracket expressions (character classes) in glob patterns.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install expand-brackets --save\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var brackets = require('expand-brackets');\n\nbrackets('[![:lower:]]');\n//=> '[^a-z]'\n</code></pre>\n<h2>.isMatch</h2>\n<p>Return true if the given string matches the bracket expression:</p>\n<pre><code class=\"language-js\">brackets.isMatch('A', '[![:lower:]]');\n//=> true\n\nbrackets.isMatch('a', '[![:lower:]]');\n//=> false\n</code></pre>\n<h2>.makeRe</h2>\n<p>Make a regular expression from a bracket expression:</p>\n<pre><code class=\"language-js\">brackets.makeRe('[![:lower:]]');\n//=> /[^a-z]/\n</code></pre>\n<p>The following named POSIX bracket expressions are supported:</p>\n<ul>\n<li><code>[:alnum:]</code>: Alphanumeric characters (<code>a-zA-Z0-9]</code>)</li>\n<li><code>[:alpha:]</code>: Alphabetic characters (<code>a-zA-Z]</code>)</li>\n<li><code>[:blank:]</code>: Space and tab (<code>[ t]</code>)</li>\n<li><code>[:digit:]</code>: Digits (<code>[0-9]</code>)</li>\n<li><code>[:lower:]</code>: Lowercase letters (<code>[a-z]</code>)</li>\n<li><code>[:punct:]</code>: Punctuation and symbols. (<code>[!\"#$%&#x26;'()*+, -./:;&#x3C;=>?@ [\\]^_``{|}~]</code>)</li>\n<li><code>[:upper:]</code>: Uppercase letters (<code>[A-Z]</code>)</li>\n<li><code>[:word:]</code>: Word characters (letters, numbers and underscores) (<code>[A-Za-z0-9_]</code>)</li>\n<li><code>[:xdigit:]</code>: Hexadecimal digits (<code>[A-Fa-f0-9]</code>)</li>\n</ul>\n<p>Collating sequences are not supported.</p>\n<h2>Related projects</h2>\n<p>You might also be interested in these projects:</p>\n<ul>\n<li><a href=\"https://www.npmjs.com/package/extglob\">extglob</a>: Convert extended globs to regex-compatible strings. Add (almost) the expressive power of regular expressions to… <a href=\"https://www.npmjs.com/package/extglob\">more</a> | <a href=\"https://github.com/jonschlinkert/extglob\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-extglob\">is-extglob</a>: Returns true if a string has an extglob. | <a href=\"https://github.com/jonschlinkert/is-extglob\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-glob\">is-glob</a>: Returns <code>true</code> if the given string looks like a glob pattern or an extglob pattern.… <a href=\"https://www.npmjs.com/package/is-glob\">more</a> | <a href=\"https://github.com/jonschlinkert/is-glob\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-posix-bracket\">is-posix-bracket</a>: Returns true if the given string is a POSIX bracket expression (POSIX character class). | <a href=\"https://github.com/jonschlinkert/is-posix-bracket\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/micromatch\">micromatch</a>: Glob matching for javascript/node.js. A drop-in replacement and faster alternative to minimatch and multimatch. Just… <a href=\"https://www.npmjs.com/package/micromatch\">more</a> | <a href=\"https://github.com/jonschlinkert/micromatch\">homepage</a></li>\n</ul>\n<h2>Contributing</h2>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"https://github.com/jonschlinkert/expand-brackets/issues/new\">please create an issue</a>.</p>\n<h2>Building docs</h2>\n<p>Generate readme and API documentation with <a href=\"https://github.com/verbose/verb\">verb</a>:</p>\n<pre><code class=\"language-sh\">$ npm install verb &#x26;&#x26; npm run docs\n</code></pre>\n<p>Or, if <a href=\"https://github.com/verbose/verb\">verb</a> is installed globally:</p>\n<pre><code class=\"language-sh\">$ verb\n</code></pre>\n<h2>Running tests</h2>\n<p>Install dev dependencies:</p>\n<pre><code class=\"language-sh\">$ npm install -d &#x26;&#x26; npm test\n</code></pre>\n<h2>Author</h2>\n<p><strong>Jon Schlinkert</strong></p>\n<ul>\n<li><a href=\"https://github.com/jonschlinkert\">github/jonschlinkert</a></li>\n<li><a href=\"http://twitter.com/jonschlinkert\">twitter/jonschlinkert</a></li>\n</ul>\n<h2>License</h2>\n<p>verb © 2016, <a href=\"https://github.com/jonschlinkert\">Jon Schlinkert</a>.\nReleased under the <a href=\"https://github.com/jonschlinkert/expand-brackets/blob/master/LICENSE\">MIT license</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb\">verb</a>, v, on April 01, 2016.</em></p>","docPath":"argo-ci/node_modules/expand-brackets/readme","proj":"argo-ci"}}

